
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; 
}

html,body{
  height: 100% !important;
  width: 100% !important;
  margin: 0;
  padding: 0;
  clear: both;
  -webkit-box-sizing: border-box !important;
          box-sizing: border-box !important;
  font-family: 'Freight Sans LF Pro Light', Helvetica, Arial, sans-serif !important
}
h1,h2,h3,h4,h5,h6{
  font-weight: 300 !important;
}
.fill_text{
  font-weight: 200 !important;
}
.text_logo{
  font-size: 2em;
}
.btn_transition{
  -webkit-animation-play-state: 5s;
          animation-play-state: 5s;
  -webkit-animation-name: animate_button;
          animation-name: animate_button;
  -webkit-animation-duration: 30s;
          animation-duration: 30s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-delay: 5s;
          animation-delay: 5s;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-timing-function: step-end(4);
          animation-timing-function: step-end(4);
}

@-webkit-keyframes animate_button{
  10%{
    background: #4a79ba;
  }
  30%{
    background: #cf9d6c;
  }
  50%{
    background: #D66EE0;
  }
  70%{
    background: #ecab54;
  }
  90%{
    background: #86BDC3;
  }
}

@keyframes animate_button{
  10%{
    background: #4a79ba;
  }
  30%{
    background: #cf9d6c;
  }
  50%{
    background: #D66EE0;
  }
  70%{
    background: #ecab54;
  }
  90%{
    background: #86BDC3;
  }
}
@-webkit-keyframes intel_button_animate{
  10%{

    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #5dc1c3;
    border-left: 1px solid #D66EE0;
    border-right: 1px solid #ecab54;
  }
  30%{
    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #ecab54;
    border-left: 1px solid #D66EE0;
    border-right: 1px solid #5dc1c3;
  }
  50%{
    border-top: 1px solid #D66EE0;
    border-bottom: 1px solid #5dc1c3;
    border-left: 1px solid #ecab54;
    border-right: 1px solid #ff6162;
  }
  70%{
    border-top: 1px solid #ecab54;
    border-bottom: 1px solid #ff6162;
    border-left: 1px solid #5dc1c3;
    border-right: 1px solid #D66EE0;
  }
  90%{
    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #D66EE0;
    border-left: 1px solid #ecab54;
    border-right: 1px solid #5dc1c3;
  }

}
@keyframes intel_button_animate{
  10%{

    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #5dc1c3;
    border-left: 1px solid #D66EE0;
    border-right: 1px solid #ecab54;
  }
  30%{
    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #ecab54;
    border-left: 1px solid #D66EE0;
    border-right: 1px solid #5dc1c3;
  }
  50%{
    border-top: 1px solid #D66EE0;
    border-bottom: 1px solid #5dc1c3;
    border-left: 1px solid #ecab54;
    border-right: 1px solid #ff6162;
  }
  70%{
    border-top: 1px solid #ecab54;
    border-bottom: 1px solid #ff6162;
    border-left: 1px solid #5dc1c3;
    border-right: 1px solid #D66EE0;
  }
  90%{
    border-top: 1px solid #ff6162;
    border-bottom: 1px solid #D66EE0;
    border-left: 1px solid #ecab54;
    border-right: 1px solid #5dc1c3;
  }

}
.full-width{
  width: 100% !important;
}
.full-height{
  height: 100% !important;
}
.full-height-vh{
