} 
.intel_button{
  padding: 10px 15px;
  border-top: 1px solid #5dc1c3;
  border-bottom: 1px solid #ff6162;
  border-left: 1px solid #ecab54;
  border-right: 1px solid #D66EE0;
  color: #ffffff;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}
.intel_button:hover{
  -webkit-animation: intel_button_animate 30s 2s infinite;
          animation: intel_button_animate 30s 2s infinite;
  text-decoration: none;
  color: #ffffff;
}
